.heading {
  position: relative;
  height: 692px;
  width: 100%;
  color: #fff;

  &__name {
    position: absolute;
    font-weight: 900;
    font-size: 64px;
    line-height: 150%;
    left: 325px;
  }

  &__text {
    position: absolute;
    top: 100px;
    left: 325px;
    font-size: 24px;
    line-height: 150%;
    margin: 22px auto 0;
  }

  &__button {
    display: block;
    position: absolute;
    top: 185px;
    left: 325px;
    background-color: #fff;
    font-size: 16px;
    font-weight: bold;
    border-radius: 64px;
    padding: 16px 65px;
    text-align: center;
    letter-spacing: 1px;
    color: #253757;
    outline: none;
    margin-top: 42px;
  }
}
